.App {
  height: fit-content;
  background-color: #262525;
  color: white;
  font-family: 'Open Sans', sans-serif;
}

.home-top {
  /* border: 2px solid yellow; */
  display: flex;
  justify-content: space-between;
  padding: 15px;
  margin: 0 10%;
}

.home-top div {
  width: 550px;
  display: flex;
}

.home-top div input {
  margin-right: 10px;
}

.main-content-container {
  display: flex;
  justify-content: space-around;
}

ul {
  list-style: none;
  padding: 0;
  width: 250px;
  align-self: flex-start;
  background-color: darkslategrey;
  padding: 20px;
  border-radius: 10px;
}

.main-content {
  display: flex;
}

.main-content p {
  align-self: center;
}

.items-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-card {
  border: 2px solid #9A8873;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  width: 70%;
  padding: 20px;
  margin-bottom: 20px;
}

.item-card img {
  max-width: 100px;
  max-height: 100px;
}

.price {
  color: red;
}

.more-info {
  text-decoration: none;
  margin-right: 10px;
}

.free-shipping {
  color: yellow;
  margin-left: 10px;
}

.items-count {
  background-color: red;
  color: white;
  border-radius: 100%;
  margin-left: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  font-weight: bolder;
}

.cart-button {
  display: flex;
  align-items: center;
}

.product-details {
  min-height: 100vh;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.product-details-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 600px;
  width: 60%;
}

.grade-label {
  margin-left: 15px;
}

.form-container {
  border: 2px solid #9A8873;
  width: 70%;
  padding: 20px;
  border-radius: 10px;
  height: 280px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.feedback {
  border: 2px solid #9A8873;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 30px;
}

.shop-cart {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.checkout-item {
  border: 2px solid #9A8873;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 30px;
}

.units {
  color: yellow;
}

.units-container {
  display: flex;
  padding: 5px;
  justify-content: space-around;
  align-items: center;
}

.units-container button {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bolder;

}

.checkout {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.checkout input {
  margin-bottom: 10px;
}

.cadastro {
  width: 70%;
  display: flex;
  flex-direction: column;
}